.video {
  margin: 0 auto 58px auto;
}

.video__wrapper {
  position: relative;
  padding: 25px 0 32px 0;

  line-height: 24px;
  font-weight: 700;
  color: $charcoal-color;

  border: solid 1px #ececec;

  &::before {
    content: "";

    position: absolute;
    top: 32px;
    left: 34px;
    width: 32px;
    height: 32px;

    background-image: url("../img/icon-video.svg");
    background-repeat: no-repeat;
    background-size: 32px auto;
  }
}

.video__headline {
  margin: 0 0 25px 91px;

  font-size: 17px;
  color: #000000;
}

.video__image-wrapper {
  margin: 0 0 27px 0;
}

.video__image {
  display: block;
}

.video__description {
  margin: 0 47px 24px 34px;

  font-size: 13px;
  font-weight: 500;
}

.video__buy {
  margin: 0 34px;
  padding: 16px 12px;

  font-size: 17px;
  line-height: 16px;
}

@media (min-width: $tablet-width) {
  .video {
    margin-bottom: 63px;
  }

  .video__wrapper {
    display: flex;
    flex-direction: column;
    padding: 0;
    padding-bottom: 46px;

    &::before {
      top: 487px;
      left: 43px;
    }
  }

  .video__image-wrapper {
    order: -1;
    margin-bottom: 37px;
  }

  .video__image {
    width: 668px;
    height: 455px;
  }

  .video__headline {
    margin: 0 107px 32px 107px;

    font-size: 20px;
  }

  .video__description {
    margin: 0 40px 34px 40px;

    font-size: 15px;
    line-height: 30px;
  }

  .video__buy {
    margin: 0 42px;
  }
}

@media (min-width: $desktop-width) {
  .video {
    margin-bottom: 105px;
  }

  .video__wrapper {
    position: relative;
    padding: 94px 57px 62px 759px;

    &::before {
      top: 42px;
      left: 761px;
    }
  }

  .video__image-wrapper {
    position: absolute;
    top: 0;
    left: 0;
  }

  .video__image {
    width: 682px;
    height: 455px;
  }

  .video__headline {
    width: 150px;
    margin: 0 0 34px 0;
  }

  .video__description {
    margin: 0 0 45px 0;
  }

  .video__buy {
    margin: 0;
  }
}
